<template>
    <el-dialog class="m-skill-dialog" :visible.sync="show" :title="title" :before-close="close">
        <el-form class="m-skill-form" ref="form" :model="form" :rules="rules" label-width="100px">
            <el-form-item label="" prop="level">
                <el-select v-model="form.level" placeholder="" style="width: 100%">
                    <el-option v-for="item in maxLevel" :key="item" :value="item" :label="` ${item} `"></el-option>
                </el-select>
            </el-form-item>
            <el-form-item label="CD" prop="cooldown" style="width: 100%">
                <el-select v-model="form.cooldown" placeholder="CD">
                    <el-option :value="60" label=" - 60"></el-option>
                    <el-option :value="30" label=" - 30"></el-option>
                    <el-option :value="10" label=" - 10"></el-option>
                    <el-option :value="0" label=""></el-option>
                </el-select>
            </el-form-item>
            <el-form-item label="" prop="damage">
                <el-input v-model="form.damage" placeholder="" />
            </el-form-item>
            <el-form-item label="" prop="cost_vigor">
                <el-input v-model="form.cost_vigor" placeholder="" />
            </el-form-item>
            <el-form-item label="" prop="cost_endurance">
                <el-input v-model="form.cost_endurance" placeholder="" />
            </el-form-item>
            <el-form-item label="" prop="hit_vigor">
                <el-input v-model="form.hit_vigor" placeholder="" />
            </el-form-item>
            <el-form-item label="" prop="hit_endurance">
                <el-input v-model="form.hit_endurance" placeholder="" />
            </el-form-item>
            <el-form-item label="" prop="link">
                <el-input v-model="form.link" placeholder="" />
            </el-form-item>
            <el-form-item label="" prop="remarks">
                <el-input type="textarea" v-model="form.remarks" placeholder="" />
            </el-form-item>
        </el-form>
        <template #footer>
            <span class="dialog-footer">
                <el-button @click="close"></el-button>
                <el-button type="primary" :loading="loading" @click="submitForm()"></el-button>
            </span>
        </template>
    </el-dialog>
</template>
<script>
import { addSkillInfo } from "@/service/baizhan";
import { cloneDeep } from "lodash";

const form = {
    user_id: 0,
    skill_id: 0,
    level: 5,
    cooldown: 0,
    damage: 0,
    cost_vigor: 0,
    cost_endurance: 0,
    hit_vigor: 0,
    hit_endurance: 0,
    link: "",
    remarks: "",
};

export default {
    name: "SkillForm",
    props: ["staged"],
    data: () => ({
        show: false,
        loading: false,
        form: cloneDeep(form),
        rules: {},
    }),
    computed: {
        maxLevel() {
            return ~~this.staged?.InSkill?.MaxLevel || 5;
        },
        title() {
            return `/${this.staged.szSkillName}/${this.staged.szBossName}`;
        },
    },
    watch: {
        visible: {
            immediate: true,
            handler: function (val) {
                this.show = val;
            },
        },
    },
    methods: {
        open(addon) {
            this.form = {
                ...form,
                ...addon,
            };
            this.show = true;
        },
        // 
        close() {
            this.show = false;
            this.$refs.form.resetFields();
        },
        submitForm() {
            this.$refs.form.validate((valid, fields) => {
                if (valid) {
                    this.edit();
                } else {
                    console.log("error submit!!!", fields);
                }
            });
        },
        // 
        edit() {
            if (this.loading) return;
            this.loading = true;
            addSkillInfo(this.form)
                .then((res) => {
                    this.$notify({
                        type: "success",
                        message: "!",
                    });
                    const { data } = res.data || {};
                    this.$emit("update", data);
                })
                .finally(() => {
                    this.loading = false;
                    this.close();
                });
        },
    },
};
</script>
